.footer_outer_container {
  background-color: #111;
  color: gray;
  display: flex;
  justify-content: center;
}
.footer_inner_container {
  width: 60%;
  /*background-color: red;*/
}
.footer_icons {
  /*  background-color: red;*/
  width: 12%;
  display: flex;
  justify-content: space-between;
  margin-left: 10px;
  color: #fff;
}
.footer_data {
  /*background-color: red; */
  display: flex;
  justify-content: space-between;
}

.footer_data ul {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 15vh;
  /*background-color: blue; */
}

.footer_data ul li {
  /*background-color: red; */
  cursor: pointer;
  list-style: none;
}

.service_code {
  display: flex;
  align-items: center;
  justify-content: center;
  border: #fff 1px solid;
  width: 10%;
  margin: 10px 0px;
  cursor: pointer;
}
.copy-write {
  font-size: 12px;
  margin-bottom: 10px;
}
